<template>
	<view class="content">
		<!-- 	<request-loading></request-loading> -->
		<view class="header">
			<image src="../../static/images/bg.png" mode="" class="header-image"></image>
			<view class="header_title">
				<image src="../../static/images/bt.png" mode=""></image>
			</view>
			<view class="header_tabbar">
				<view class="header_tabbar_ul">
					<view class="header_tabbar_li" id="findProject" @click="toggle('bottom')">
						<view>
							<image src="../../static/images/tb1.png" alt="">
								<text class="tab_title">找项目</text>
						</view>
					</view>
					<view class="header_tabbar_li" id="findPolicy">
						<view>
							<image src="../../static/images/tb2.png" alt="" @click="toggle('bottom')">
								<text class="tab_title">查政策</text>
						</view>
					</view>
					<view class="header_tabbar_li" id="search">
						<view>
							<image src="../../static/images/tb3.png" alt="" @click="toggle('bottom')">
								<text class="tab_title">综合查询</text>
						</view>
					</view>
					<view class="header_tabbar_li" id="result">
						<view>
							<image src="../../static/images/tb4.png" alt="" @click="toggle('bottom')">
								<text class="tab_title">交易结果</text>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="mian">
			<view class="ktitle_title">
				<view></view> <text>新闻动态</text>
			</view>
			<view class="loginformore">
				<button class="button" type="primary" @click="toggle('bottom')"><text
						class="button-text">登录后即可查看更多</text></button>
			</view>
			<view class="" style="height: 30rpx;">

			</view>
		</view>
		<view>
			<!-- 普通弹窗 -->
			<uni-popup ref="popup" background-color="#fff" :maskClick="false">
				<view class="popup-content" :class="{ 'popup-height': type === 'left' || type === 'right' }">
					<view class="loginmsg">
						<view class="msg">是否同意授权椒江区小程序获取手机号信息？</view>
						<view class="uni-flex uni-row">
							<view class="refuse" @click="close">
								<button type="warn">拒绝</button>
							</view>
							<view class="agree" @click="submit()">
								<button type="primary" open-type="getPhoneNumber"
									@getphonenumber="onGetPhoneNumber">同意</button>
							</view>
						</view>
					</view>
				</view>
			</uni-popup>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				value: '',
				type: '',
			}
		},
		onload() {
			console.log(1)
			// 微信登录
			
			uni.login({
				success: (res) => {
					if (res.code) { //微信登录成功 已拿到code  
						uni.request({
							url: 'https://www.xx123.com//common/unionId.do', //演示地址，请以你的后端接口为准  
							method: 'POST',
							data: {
								code: res.code //wx.login 登录成功后的code  
							},
							success: (cts) => {
								// 换取成功后 暂存这些数据 留作后续操作  
								this.openid = cts.data.openid //openid 用户唯一标识  
								this.unionid = cts.data.unionid //unionid 开放平台唯一标识  
								this.session_key = cts.data.session_key //session_key  会话密钥  
							}
						});
					} else {
						console.log('登录失败！' + res.errMsg)
					}
				}
			})
		},
		mounted() {
			console.log(1)
			// 微信登录
			uni.login({
				success: (res) => {
					if (res.code) { //微信登录成功 已拿到code  
						uni.request({
							url: 'https://www.xx123.com//common/unionId.do', //演示地址，请以你的后端接口为准  
							method: 'POST',
							data: {
								code: res.code //wx.login 登录成功后的code  
							},
							success: (cts) => {
								// 换取成功后 暂存这些数据 留作后续操作  
								this.openid = cts.data.openid //openid 用户唯一标识  
								this.unionid = cts.data.unionid //unionid 开放平台唯一标识  
								this.session_key = cts.data.session_key //session_key  会话密钥  
							}
						});
					} else {
						console.log('登录失败！' + res.errMsg)
					}
				}
			})
		},
		methods: {
			toggle(type) {
				this.type = type
				// open 方法传入参数 等同在 uni-popup 组件上绑定 type属性
				this.$refs.popup.open(type)
			},
			close() {
				this.$refs.popup.close()
			},
			// 微信登录后,获取用户信息
			onGetPhoneNumber(e) {
				if (e.detail.errMsg == "getPhoneNumber:fail user deny") { //用户决绝授权  

					//拒绝授权后弹出一些提示  

				} else { //允许授权  
					uni.request({
						url: 'https://www.xx123.com//common/decode.do', //演示地址，请以你的后端接口为准  
						method: 'POST',
						data: {
							iv: e.detail.iv,
							encryptedData: e.detail.encryptedData,
							session: this.session_key,
						},
						success: (res) => {
							console.log(res.data) //res.data 即为后端返回的解密数据  
						}
					});

				}
			},
			submit() {
				uni.navigateTo({
					url: 'userlogin',
				})
			}
		},
	}
</script>

<style scoped>
	body {
		background: #fff !important;
	}

	uni-page-body {
		background: #FFFFFF !important;
	}

	.content {
		background: #FFFFFF;
	}

	/* 首页头部 */
	.header {
		height: 590rpx;
		background-size: 100% 100%;
		position: relative;
	}

	.header-image {
		height: 590rpx;
		width: 100%;
	}

	.header_title {
		position: absolute;
		top: 0;
		height: 200rpx;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.header_title image {
		margin-top: 40rpx;
		text-align: center;
		height: 160rpx;
		width: 59%;
	}

	.header_tabbar {
		position: absolute;
		top: 250rpx;
		width: 100%;
	}

	.header_tabbar .header_tabbar_ul {
		display: flex;
		justify-content: space-around;
		align-items: center;
		margin-top: 10rpx;
	}

	.header_tabbar_li {
		width: 25%;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.header_tabbar_li view {
		display: flex;
		flex-direction: column;
		align-items: center;
	}


	.header_tabbar_li image {
		width: 80rpx;
		height: 80rpx;
	}

	.header_tabbar_li .tab_title {
		display: inline-block;
		width: 100%;
		color: #fff;
		font-size: 22rpx;
		margin-top: 10rpx;
		text-align: center;
	}

	/* mian */
	.ktitle_title {
		overflow: hidden;
		margin-top: 43rpx;
		padding: 0 28rpx;
	}

	.ktitle_title view {
		width: 8rpx;
		height: 30rpx;
		margin-top: 15rpx;
		background: #1e69db;
		float: left;
	}

	.ktitle_title text {
		float: left;
		margin-left: 20rpx;
		font-size: 32rpx;
		font-weight: bold;
		color: #3b89ff;
	}

	.loginmsg {
		text-align: center;
		padding: 16rpx;
		height: 260rpx;

	}

	.loginmsg .msg {
		margin-top: 20rpx;
		font-size: 32rpx;
	}

	.refuse,
	.agree {
		flex: 1
	}

	.loginmsg button {
		margin-top: 70rpx;
		width: 150rpx;
		height: 60rpx;
		line-height: 60rpx;
	}

	.loginformore {
		width: 80%;
		text-align: center;
		margin: 20rpx auto;
	}
</style>
